.folded-bg {
  display: flex;
  background-image: url("../../img/folded_paper.png");
  background-size: cover;
  width: 100%;
  height: 100%;
  padding: 7%;
  padding-top: 25%;
  margin-right: 5% !important;
  margin-top: 110px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.saved-container {
  padding: 25% 0 2% 5%;
}

.delete-btn{
  font-weight: 400;
  color: whitesmoke;
  background-image: linear-gradient(to right, #093028, #237a57); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */;
  outline: none;
  margin-top: 5%;
  margin-bottom: 10%
}


@media only screen and (max-width: 960px) {
  .folded-bg {
    display: none;
  }
}